import ACTIONS from "./action";
const defaultState = {
  total: 0,
  expression: '',
  isStart: true,
  isZero: true,
  isWhole: true,
  onOperation: false,
  displayedValue: 0
};
const calculatorReducer = (state = defaultState, action) => {
  let exp = state.expression;
  switch (action.type) {
    case ACTIONS.Types.ADD_TO_DISPLAY: {
      console.log(action);
      return { 
        total: state.total,
        expression: state.expression,
        isStart: state.isStart,
        isZero: state.isZero,
        isWhole: state.isWhole,
        onOperation: state.onOperation,
        displayedValue: state.displayedValue.concat(action.symbol)
      };
    }
    case ACTIONS.Types.REPLACE_DISPLAY: {
      console.log(action);
      return { 
        total: state.total,
        expression: state.expression,
        isStart: state.isStart,
        isZero: state.isZero,
        isWhole: state.isWhole,
        onOperation: state.onOperation,
        displayedValue: action.symbol
      };
    }
    case ACTIONS.Types.ADD: {
      console.log(action);
      return { 
        total: state.total,
        expression: exp.concat(action.symbol),
        isStart: false,
        isZero: state.isZero,
        isWhole: state.isWhole,
        onOperation: state.onOperation,
        displayedValue: state.displayedValue
      };
    }
    case ACTIONS.Types.REPLACE: {
      console.log(action);
      return { 
        total: state.total,
        expression: exp.slice(0, exp.length - 1).concat(action.symbol),
        isStart: false,
        isZero: state.isZero,
        isWhole: state.isWhole,
        onOperation: state.onOperation,
        displayedValue: state.displayedValue
      };
    }
    case ACTIONS.Types.REPLACE_EXPRESSION_WITH: {
      console.log(action);
      return { 
        total: state.total,
        expression: action.symbol,
        isStart: false,
        isZero: state.isZero,
        isWhole: state.isWhole,
        onOperation: state.onOperation,
        displayedValue: state.displayedValue
      };
    }
    case ACTIONS.Types.OPERATION_SWITCH: {
      console.log(action);
      return { 
        total: state.total,
        expression: state.expression,
        isStart: state.isStart,
        isZero: state.isZero,
        isWhole: state.isWhole,
        onOperation: !state.onOperation,
        displayedValue: state.displayedValue
      };
    }
    case ACTIONS.Types.ZERO_SWITCH_ON: {
      console.log(action);
      return { 
        total: state.total,
        expression: state.expression,
        isStart: state.isStart,
        isZero: true,
        isWhole: state.isWhole,
        onOperation: state.onOperation,
        displayedValue: state.displayedValue
      };
    }
    case ACTIONS.Types.ZERO_SWITCH_OFF: {
      console.log(action);
      return { 
        total: state.total,
        expression: state.expression,
        isStart: state.isStart,
        isZero: false,
        isWhole: state.isWhole,
        onOperation: state.onOperation,
        displayedValue: state.displayedValue
      };
    }
    case ACTIONS.Types.WHOLE_SWITCH_ON: {
      console.log(action);
      return { 
        total: state.total,
        expression: state.expression,
        isStart: state.isStart,
        isZero: state.isZero,
        isWhole: true,
        onOperation: state.onOperation,
        displayedValue: state.displayedValue
      };
    }
    case ACTIONS.Types.WHOLE_SWITCH_OFF: {
      console.log(action);
      return { 
        total: state.total,
        expression: state.expression,
        isStart: state.isStart,
        isZero: state.isZero,
        isWhole: false,
        onOperation: state.onOperation,
        displayedValue: state.displayedValue
      };
    }
    case ACTIONS.Types.AC: {
      console.log(action);
      return defaultState;
    }
    case ACTIONS.Types.TOTAL: {
      console.log(action);
      return { 
        // eslint-disable-next-line
        total: eval(state.expression),
        // eslint-disable-next-line
        expression: state.expression + ' = ' + eval(state.expression),
        isStart: true,
        isZero: true,
        isWhole: true,
        onOperation: false,
        // eslint-disable-next-line
        displayedValue: eval(state.expression)
      };
    }
    default:
      return state;
  }
};
export default calculatorReducer;