// types of action
const Types = {
  ADD: "ADD",
  REPLACE: "REPLACE",
  ADD_TO_DISPLAY: "ADD_TO_DISPLAY",
  REPLACE_DISPLAY: "REPLACE_DISPLAY",
  REPLACE_EXPRESSION_WITH: "REPLACE_EXPRESSION_WITH",
  OPERATION_SWITCH: "OPERATION_SWITCH",
  ZERO_SWITCH_ON: "ZERO_SWITCH_ON",
  ZERO_SWITCH_OFF: "ZERO_SWITCH_OFF",
  WHOLE_SWITCH_ON: "WHOLE_SWITCH_ON",
  WHOLE_SWITCH_OFF: "WHOLE_SWITCH_OFF",
  AC: "AC",
  TOTAL: "TOTAL"
};
// actions 
const addToDisplay = symbol => ({
  type: Types.ADD_TO_DISPLAY,
  symbol
});
const replaceDisplayWith = symbol => ({
  type: Types.REPLACE_DISPLAY,
  symbol
});
const addToExpression = symbol => ({
  type: Types.ADD,
  symbol
});
const replaceInExpression = symbol => ({
  type: Types.REPLACE,
  symbol
});
const replaceExpressionWith = symbol => ({
  type: Types.REPLACE_EXPRESSION_WITH,
  symbol
});
const operationSwitch = () => ({
  type: Types.OPERATION_SWITCH
});
const zeroSwitchOn = () => ({
  type: Types.ZERO_SWITCH_ON
});
const zeroSwitchOff = () => ({
  type: Types.ZERO_SWITCH_OFF
});
const wholeSwitchOn = () => ({
  type: Types.WHOLE_SWITCH_ON
});
const wholeSwitchOff = () => ({
  type: Types.WHOLE_SWITCH_OFF
});
const ac = () => ({
  type: Types.AC
});
const total = () => ({
  type: Types.TOTAL
});
export default {
  addToDisplay,
  replaceDisplayWith,
  addToExpression,
  replaceInExpression,
  replaceExpressionWith,
  operationSwitch,
  zeroSwitchOn,
  zeroSwitchOff,
  wholeSwitchOn,
  wholeSwitchOff,
  ac,
  total,
  Types
};