import React, { Component } from "react";
import ACTIONS from "../modules/action";
import { connect } from "react-redux";

class Zero extends Component {
  constructor(props){
    super(props);
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(event) {
    if(this.props.isStart){
      this.props.addToExpression(event.target.value);
      this.props.replaceDisplayWith(event.target.value);
      this.props.zeroSwitchOn();
    } else if(this.props.isZero){
      this.props.replaceInExpression(event.target.value);  
      this.props.replaceDisplayWith(event.target.value);  
    } else if(this.props.onOperation){
      this.props.operationSwitch();
      this.props.zeroSwitchOn();  
      this.props.addToExpression(event.target.value);
      this.props.replaceDisplayWith(event.target.value);  
    } else {
      this.props.addToExpression(event.target.value);
      this.props.addToDisplay(event.target.value);
    }
  };

  render() {
    return (
        <button className="digit" id='zero' onClick={this.handleClick} value="0">0</button>
    );
  }
}


const mapStateToProps = state => ({
  onOperation: state.onOperation,
  isStart: state.isStart,
  isZero: state.isZero
});
const mapDispatchToProps = dispatch => ({
  addToExpression: symbol => dispatch(ACTIONS.addToExpression(symbol)),
  replaceInExpression: symbol => dispatch(ACTIONS.replaceInExpression(symbol)),
  addToDisplay: symbol => dispatch(ACTIONS.addToDisplay(symbol)),
  replaceDisplayWith: symbol => dispatch(ACTIONS.replaceDisplayWith(symbol)),
  operationSwitch: () => dispatch(ACTIONS.operationSwitch()),
  zeroSwitchOn: () => dispatch(ACTIONS.zeroSwitchOn())
});
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Zero);