import React, { Component } from "react";
import ACTIONS from "../modules/action";
import { connect } from "react-redux";

class DecimalPoint extends Component {
  constructor(props){
    super(props);
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(event) {
    if(this.props.isWhole) {
      if(this.props.isStart) {
        this.props.replaceExpressionWith('0' + event.target.value);
        this.props.replaceDisplayWith('0' + event.target.value);
      } else if(this.props.onOperation){ 
        this.props.operationSwitch(); 
        this.props.addToExpression('0' + event.target.value);
        this.props.replaceDisplayWith('0' + event.target.value);
      } else {
        this.props.addToExpression(event.target.value);
        this.props.addToDisplay(event.target.value);
      }
      this.props.zeroSwitchOff();
      this.props.wholeSwitchOff();
    }
  };

  render() {
    return (
        <button id="decimal" onClick={this.handleClick} value='.'>.</button>
    );
  }
}


const mapStateToProps = state => ({
  onOperation: state.onOperation,
  isStart: state.isStart,
  isWhole: state.isWhole
});
const mapDispatchToProps = dispatch => ({
  addToExpression: symbol => dispatch(ACTIONS.addToExpression(symbol)),
  addToDisplay: symbol => dispatch(ACTIONS.addToDisplay(symbol)),
  replaceDisplayWith: symbol => dispatch(ACTIONS.replaceDisplayWith(symbol)),
  replaceExpressionWith: symbol => dispatch(ACTIONS.replaceExpressionWith(symbol)),
  operationSwitch: () => dispatch(ACTIONS.operationSwitch()),
  zeroSwitchOff: () => dispatch(ACTIONS.zeroSwitchOff()),
  wholeSwitchOff: () => dispatch(ACTIONS.wholeSwitchOff())
});
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(DecimalPoint);