
import React, { Component } from "react";
import "./App.css";
import Operation from "./pages/operation";
import Digit from "./pages/digit";
import Zero from "./pages/zero";
import AC from "./pages/ac";
import Equals from "./pages/equals";
import DecimalPoint from "./pages/decimalPoint";
import Display from "./pages/display";
import { Provider as ReduxProvider } from "react-redux";
import configureStore from "./modules/store";
const reduxStore = configureStore(window.REDUX_INITIAL_DATA);
class App extends Component {
  render() {
    return (
      <ReduxProvider store={reduxStore}>
          <div id='calculator'>
            <Display />
            <Operation op='+' id='add' />
            <Operation op='-' id='subtract' />
            <Operation op='*' id='multiply' />
            <Operation op='/' id='divide' />
            <Digit number='1' id='one' />
            <Digit number='2' id='two' />
            <Digit number='3' id='three' />
            <Digit number='4' id='four' />
            <Digit number='5' id='five' />
            <Digit number='6' id='six' />
            <Digit number='7' id='seven' />
            <Digit number='8' id='eight' />
            <Digit number='9' id='nine' />
            <Zero />
            <DecimalPoint />
            <Equals />
            <AC />
          </div>
      </ReduxProvider>
    );
  }
}
export default App;
